@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed|Roboto+Mono|Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-container div {
  margin-top: 40px; }

.login-container input {
  border: none;
  border-bottom: 1px solid #93B7CC;
  margin-top: 20px;
  color: #93B7CC;
  font-weight: bolder; }

.login-container button {
  border: none;
  flex-direction: row;
  justify-content: right;
  color: #93B7CC;
  font-weight: bolder;
  float: right;
  font-size: 1em;
  cursor: pointer; }

.login-container label {
  position: absolute;
  font-size: 10px; }

.weather-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  text-transform: uppercase;
  padding: 0;
  margin: 0;
  list-style: none;
  display: -moz-flex;
  display: flex;
  -webkit-justify-content: space-around;
  -webkit-align-items: stretch;
  width: 100%; }
  .weather-info .description-row {
    font-size: 2em;
    justify-content: center;
    align-items: center;
    text-align: center; }
  .weather-info .row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
  .weather-info .column {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1 1;
    padding: 20px;
    flex-grow: 3; }
  .weather-info .weather-icon, .weather-info .weather-description {
    color: #3893B1; }
  .weather-info .blue-highlight {
    color: #93B7CC;
    text-transform: none; }
  .weather-info .weather-description {
    font-size: 11px; }
  .weather-info .sun-stages {
    width: 100%;
    font-size: 1em !important; }
    .weather-info .sun-stages .weather-sys-info {
      color: #3893B1;
      font-size: 0.6em;
      line-height: 2;
      margin-left: 10px; }
    .weather-info .sun-stages .wi-sunrise, .weather-info .sun-stages .wi-sunset {
      font-size: 20px;
      line-height: 2;
      margin-left: -5px; }

@media all and (min-width: 0px) and (max-width: 680px) {
  .weather-info .row {
    flex-direction: column; }
    .weather-info .row .column .middle-row {
      order: 1; }
    .weather-info .row .column .weather-info {
      order: 2; } }

.home-page-container p {
  color: #93B7CC; }
  .home-page-container p a {
    text-decoration: none;
    text-transform: none !important; }
    .home-page-container p a svg {
      cursor: pointer; }
      .home-page-container p a svg path {
        color: #73F441; }

.add-page-container {
  width: 100%;
  text-transform: none; }
  .add-page-container .form-container {
    display: inline-block;
    text-align: left;
    top: 90px;
    position: absolute;
    left: 35%;
    width: 25%; }
    .add-page-container .form-container input {
      color: #93B7CC;
      width: 100%;
      border: none;
      border-bottom: 1px solid #267DBD;
      padding: 10px; }
      .add-page-container .form-container input:focus {
        outline: none; }
    .add-page-container .form-container .suggestions-list {
      position: relative;
      top: -30px; }
      .add-page-container .form-container .suggestions-list ul {
        padding: 0; }
        .add-page-container .form-container .suggestions-list ul li {
          list-style: none;
          color: #93B7CC;
          cursor: pointer; }
    .add-page-container .form-container .save-city-button {
      float: right; }
      .add-page-container .form-container .save-city-button button {
        border: none;
        color: #3893B1;
        font-size: 16px;
        font-weight: bolder; }
  .add-page-container .add-page-input svg {
    position: relative;
    right: -95%;
    top: 55px; }
    .add-page-container .add-page-input svg path {
      color: #93B7CC; }

@media all and (min-width: 0px) and (max-width: 680px) {
  .add-page-container .form-container {
    display: inline-block;
    left: 0;
    right: 0;
    text-align: left;
    position: absolute;
    width: 85%;
    padding: 20px; }
    .add-page-container .form-container input {
      width: 90%; }
    .add-page-container .form-container svg {
      position: relative;
      right: -85%;
      top: 55px; } }

* {
  background: #132434;
  font-family: Roboto, sans-serif;
  color: #267DBD;
  font-size: 1.1em; }

#wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  text-transform: uppercase; }
  #wrapper .page-not-found p {
    color: #93B7CC; }
  #wrapper .back-button {
    position: absolute;
    top: 5%;
    left: 5%; }
    #wrapper .back-button a {
      text-decoration: none;
      font-weight: bolder;
      font-size: 1.5em; }
    #wrapper .back-button svg path {
      color: #3893B1; }

